@import "mixins.scss";
@import "variables.scss";

.page-header {
  background-color: $bg_dark_blue;

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0;
  }

  &__logo {
    display: flex;
    padding: 0;
    margin: 0;
    height: 66px + 3px; //+3 'это только для открытого типа
    //opacity: 0.3; это для закрытого типа
    box-shadow: 0 1px 0 0 $color_black;

    &-image {
      padding-top: 22px;
      padding-left: 20px;
    }
  }
}

@media (min-width: $tablet-width) {
  .page-header {
    &__logo {
      height: 109px;
    }
  }
}

@media (min-width: $desktop-width) {
  .page-header {
    background-color: rgba($bg_dark_blue, 0.7);

    &__wrapper {
      flex-direction: row;
      padding: 0 130px;
    }
  }
}
